.ceo {
  text-align: left;
  width: 100%;
}

.ceoBox {
  margin: 0 15%;
}

.ceo h1 {
  font-weight: bold;
  font-size: 2.3vw;
}

.txtBox {
  margin: 5vw 0;
}

.txtBox h1 {
  margin-bottom: 4vw;
}

.txtBox img {
  width: 75vw;
  height: 21vw;
  border-radius: 15px;
  object-fit: cover;
}

.txtBox2 {
  display: flex;
  justify-content: space-between;
  width: 95%;
  margin-bottom: 3vw;
}
.txtBox2 p {
  font-size: 1.3vw;
}
.txtBox2 em {
  font-weight: bold;
}

.txt {
  margin: 2vw 0;
}
.txtCeo {
  width: 35vw;
  font-size: 1.1vw !important;
}

/* 애니메이션 */
/* ceo.module.css */

.ceo {
  position: relative;
  overflow: hidden; /* Ensure that animations are contained */
}

/* For txtBox h1 and ceoBg */
.txtBox {
  animation: slideUp 1s ease-out forwards;
}

.txtBox img {
  animation: fadeIn 2s ease-out forwards;
}

/* Animation for txtBox2 h1 */
.txtBox2 h1 {
  animation: slideInFromLeft 1s ease-out forwards;
}

/* Animation for txtCeo */
.txtCeo {
  animation: slideInFromRight 1s ease-out forwards;
}

/* Keyframes for the animations */
@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideInFromLeft {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideInFromRight {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
