/* txtBox 및 텍스트들 */

.txtBox {
  z-index: 1;
  width: 50%;
  height: 100vh;
  background-color: #f0efee;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.txts {
  width: 60%;
  /* 4.9vw = 94px */
  text-align: left;
}

.txts a {
  position: relative;
  top: 3vw;
  color: white;
  padding: 0.8vw 3vw;
  border-radius: 5vw;
  background-color: #67382a;
  font-size: 0.9vw;
}

.txts h1 {
  font-size: 2.3vw;
  font-weight: bold;
  /* 450px = 23.4vw */
  width: 23.4vw;
  margin-bottom: 1vw;
}

.txts p {
  color: #666666;
  font-size: 1vw;
}

/* 플렉스 전용 */

.main2flex {
  display: flex;
  align-items: center;
}

/* 스와이퍼 애니메이션 - active */

@keyframes slideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.active h1,
.active p,
.active a {
  opacity: 0;
  animation: slideUp 1s forwards;
}

.active h1 {
  animation-delay: 0.5s;
}

.active p {
  animation-delay: 1s;
}

.active a {
  animation-delay: 1.5s;
}

/* 이미지 및 이미지 애니메이션 */

.imgBox {
  position: relative;
  z-index: 2;
  /* 54.3vw = 1042px */
  width: 50vw;
  /* 32.9vw = 631px */
  height: 32.9vw;
}
.imgBox img {
  position: fixed;
  z-index: 2;
  /* 54.3vw = 1042px */
  width: 53vw;
  /* 32.9vw = 631px */
  height: 32.9vw;
}

.imgBox img {
  /* 초기 위치 설정 */
  transform: translateY(100%);
  opacity: 0;
  /* 애니메이션 설정 */
  transition: transform 1s ease, opacity 1s ease;
}

.active .imgBox img {
  transform: translateY(0);
  opacity: 1;
}
