.tech {
  width: 100%;
}
.techBox {
  margin: 0 15%;
  margin-top: 5vw;
}
.info {
  height: 33vw;
  width: 33.3vw;
  text-align: left;
}
.info h1 {
  font-size: 2vw;
  font-weight: bold;
  border-bottom: 2px solid;
}
.info h2 {
  width: 5vw;
  font-weight: bold;
  margin-right: 2vw;
}
.info p {
  color: #444444;
  font-weight: 300;
}
.txt {
  display: flex;
  font-size: 1vw;
  align-items: baseline;
  justify-content: left;
  border-bottom: 1px solid #00000030;
  padding: 1vw 0;
}
.flex {
  display: flex;
  justify-content: space-between;
}

.imgBox {
  display: flex;
  align-items: center;
  width: 29.2vw;
  height: 33vw;
  background-color: #efefef;
  border-radius: 20px;
}
.btnBox {
  display: flex;
  justify-content: space-between;
  text-align: center;
  line-height: 3;
  margin-top: 5vw;
}
.btn {
  background-color: #67382a;
  color: white;
  font-weight: bold;
  font-size: 1vw;
  border-radius: 15px;
  width: 16vw;
  height: 3vw;
}
.downBtn {
  background-color: #fff;
  color: #67382a;
  border-radius: 15px;
  border: 1px solid #67382a;
}
.more {
  line-height: 2;
  margin: 10vw 0;
}
.more h2 {
  margin-top: 2vw;
  font-size: 1.6vw;
  font-weight: bold;
}
.more p {
  font-size: 1.3vw;
}
.moreBox {
  width: 18vw;
  height: 18vw;
  border-radius: 50%;
  background-color: #67382a15;
}
.moreBox img {
  margin-top: 25%;
  width: 8vw;
  height: 8vw;
}
/* 애니메이션 */

/* tech.module.css */

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.imgBox,
.info,
.btnBox,
.more {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
}

.imgBox {
  animation-delay: 0.2s;
}

.info {
  animation-delay: 0.4s;
}

.btnBox {
  animation-delay: 0.6s;
}
.more:nth-child(1) {
  animation-delay: 1.2s;
}
.more:nth-child(2) {
  animation-delay: 1.4s;
}
.more:nth-child(3) {
  animation-delay: 1.6s;
}
