.info {
  width: 100%;
  text-align: left;
}
.infoTop {
  background: url(../../images/infoBg1.png) no-repeat center/cover;
  position: relative;
  padding: 5vw 0;
}

.txtTop {
  display: flex;
  margin: 5vw 0;
}
.txtTop h1 {
  font-size: 2.3vw;
  margin-right: 15vw;
  font-weight: bold;
}
.txtTop p {
  font-size: 1.3vw;
}
.infoTopBox {
  position: relative;
}
.infoTopBox .txtBox {
  margin-left: 2vw;
}
.infoTopBox .txtBox h2 {
  font-size: 1.4vw;
}
.infoTopBox .txtBox p {
  font-size: 1.1vw;
}

.txtBox h2 {
  font-size: 1.6vw;
  font-weight: bold;
}
.infoBox1 {
  display: flex;
  align-items: center;
  position: relative;
  top: 3vw;
}
.infoBox2 {
  display: flex;
  align-items: center;
  position: relative;
  left: 5vw;
}
.infoBox3 {
  display: flex;
  align-items: center;
  position: relative;
  left: 10vw;
  bottom: 3vw;
}

.infoImgBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 15vw;
  height: 15vw;
  border: 2px solid #67382a;
  border-radius: 50%;
}
.infoImgBox img {
  width: 5vw;
  height: 5vw;
}
.infoImgBox h2 {
  margin-top: 1vw;
  font-weight: bold;
  font-size: 1.6vw;
  color: #67382a;
}
.infoTopBox {
  margin: 0 15%;
}

.infoMid {
  margin-bottom: 10vw;
  margin: 10vw 15%;
}
.infoMid .txtBox h1 {
  margin-bottom: 1vw;
}
.infoMid .txtBox {
  margin: 0;
  margin-bottom: 2vw;
}
.infoMidItem img {
  width: 5vw;
  height: 5vw;
  border-radius: 0px !important;
}
.infoBottom {
  position: relative;
  z-index: 1;
  color: white;
}

.video {
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 101%;
  /* 화면채우는요소 */
  object-fit: cover;
}

.infoMid img {
  border-radius: 25px;
  margin-top: 2vw;
  margin-left: 2vw;
}
.infoMidItemTxt {
  margin-left: 2vw;
}
.infoMidItemTxt h2 {
  font-size: 1.6vw;
  margin: 1vw 0;
  font-weight: bold;
}
.infoMidItemTxt p {
  font-size: 1vw;
}

.infoMidBox {
  display: flex;
  justify-content: space-between;
}
.infoMidItem {
  width: 15vw;
  height: 15vw;
  border: 0.5px solid #a9a9a9;
  border-radius: 15px;
}

.infoBox {
  margin: 0 15%;
}

.txtBox h1 {
  font-size: 2.3vw;
  font-weight: bold;
}
.txtBox2 p {
  font-size: 1.3vw;
}
.txtBox p {
  font-size: 1.3vw;
  font-weight: light;
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flex2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 15%;
  height: 60vh;
}

.infoBottom a {
  position: relative;
  top: 2vw;
  padding: 0.5vw 2vw;
  border-radius: 25px;
  border: 1px solid white;
  font-weight: bold;
}
.infoBottom .txtBox {
  position: relative;
}
.infoBottom .txtBox::after {
  content: "";
  position: absolute;
  top: -5vw;
  left: 250%;
  width: 1px;
  height: 15vw;
  background-color: rgba(255, 255, 255, 0.5);
}

/* 애니메이션 */

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInFromLeft {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.infoBox h1 {
  opacity: 0;
  animation: slideUp 0.5s ease-in-out forwards;
}
.infoBox p {
  opacity: 0;
  animation: slideUp 0.5s ease-in-out forwards;
  animation-delay: 0.3s;
}

/* info.module.css */
.infoBox1,
.infoBox2,
.infoBox3,
.infoMid .txtBox p,
.infoMid.animated .infoMidItem {
  opacity: 0;
}

.infoTop.animated .infoBox1 {
  animation: slideInFromLeft 0.5s ease-in-out forwards;
}
.infoTop.animated .infoBox2 {
  animation: slideInFromLeft 0.5s ease-in-out forwards;
  animation-delay: 0.3s;
}
.infoTop.animated .infoBox3 {
  animation: slideInFromLeft 0.5s ease-in-out forwards;
  animation-delay: 0.6s;
}

.infoMid.animated h1 {
  animation: slideUp 0.5s ease-in-out forwards;
}
.infoMid.animated .txtBox p {
  animation: slideUp 0.5s ease-in-out forwards;
  animation-delay: 0.3s;
}

.infoMid.animated .infoMidItem:nth-child(1) {
  animation: slideUp 0.5s ease-in-out forwards;
  animation-delay: 0.5s;
}
.infoMid.animated .infoMidItem:nth-child(2) {
  animation: slideUp 0.5s ease-in-out forwards;
  animation-delay: 0.7s;
}
.infoMid.animated .infoMidItem:nth-child(3) {
  animation: slideUp 0.5s ease-in-out forwards;
  animation-delay: 0.9s;
}
.infoMid.animated .infoMidItem:nth-child(4) {
  animation: slideUp 0.5s ease-in-out forwards;
  animation-delay: 1.1s;
}
