.notice {
  width: 100%;
  text-align: left;
}

.txtBox {
  margin: 2vw 0;
}

.txtBox h1 {
  font-weight: bold;
  font-size: 2.3vw;
}
.txtBox p {
  font-size: 1.2vw;
  color: #444444;
}

.noticeBox {
  margin: 0 15%;
}

.links {
  border-top: 1px solid;
  margin-bottom: 5vw;
}

.linkBox {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 0.5px solid #c7c7c7;
  height: 8vh;
}

.linkBox h2 {
  position: relative;
  right: 15vw;
  width: 20vw;
  font-weight: bold;
}
.linkBox p {
  font-size: 1vw;
  font-weight: 600;
  color: #444444;
}

.linkBox img {
  width: 1vw;
  height: 1vw;
}
/* 애니메이션 */

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.txtBox h1,
.txtBox p,
.linkBox {
  opacity: 0;
}

.txtBox h1 {
  animation: slideUp 0.5s ease-in-out forwards;
}

.txtBox p {
  animation: slideUp 0.5s ease-in-out forwards;
  animation-delay: 0.3s;
}

.linkBox:nth-last-child(4) {
  animation: slideUp 0.5s ease-in-out forwards;
  animation-delay: 0.5s;
}
.linkBox:nth-last-child(3) {
  animation: slideUp 0.5s ease-in-out forwards;
  animation-delay: 0.7s;
}
.linkBox:nth-last-child(2) {
  animation: slideUp 0.5s ease-in-out forwards;
  animation-delay: 0.9s;
}
.linkBox:nth-last-child(1) {
  animation: slideUp 0.5s ease-in-out forwards;
  animation-delay: 1.1s;
}
