.tech {
  width: 100%;
  text-align: left;
}
.techBox {
  margin: 0 15%;
  border-bottom: 2px solid #333333;
}
.txtBox h1 {
  font-weight: bold;
  font-size: 2.3vw;
  margin-top: 3vw;
}
.txtBox p {
  font-size: 1.3vw;
  color: #444444;
  margin: 2vw 0;
}

.support {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 10vh;
  border-bottom: 1px solid #00000030;
}
.support p {
  margin: 0 5%;
  margin-right: 4%;
  font-size: 0.8vw;
  color: #444444;
}
.supportTop {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 8vh;
  background-color: #f4f4f4;
  border-top: 2px solid #333333;
}

.supportTop h2 {
  margin: 0 4%;
  font-weight: bold;
}
.supportTitle {
  width: 50vw;
  cursor: pointer;
}
.supportTitle:hover {
  text-decoration: underline;
}

.supportNum {
  width: 1vw;
  height: 1vw;
}
.pagination {
  display: flex;
  justify-content: center;
  margin: 20px;
}

.pagination button {
  margin: 0 10px;
  padding: 0.6vw 0.8vw;
  border-radius: 10px;
  border: none;
  border: 1px solid #afafaf;
  color: #afafaf;
  background-color: transparent;
  cursor: pointer;
}

.pagination button:hover {
  background-color: #ddd;
}

.pagination .activePage {
  background-color: #afafaf;
  color: white;
}
/* 애니메이션 */
@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.txtBox h1,
.txtBox p {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
}
.txtBox h1 {
  animation-delay: 0.2s;
}

.txtBox p {
  animation-delay: 0.4s;
}
