/* top */
.company {
  text-align: left;
}

.companyTop {
  margin: 5vw 15%;
}
.company h1 {
  font-weight: bold;
  font-size: 2.3vw;
}

.txtBox p {
  margin: 2vw 0;
  font-size: 1.3vw;
}

.flex {
  display: flex;
  justify-content: left;
  align-items: center;
}

.intro {
  width: 25vw;
  height: 17vw;
  border: 1px solid #a9a9a9;
  border-radius: 10px;
  padding: 2vw;
  position: relative;
  margin-right: 1vw;
}
.intro img {
  width: 4vw;
  height: 4vw;
  object-fit: contain;
}

.intro h2 {
  font-weight: bold;
  font-size: 1.6vw;
  margin: 1vw 0;
  margin-top: 3vw;
}

.intro p {
  font-size: 1vw;
}

.intro:nth-child(1)::after {
  position: absolute;
  top: 2.5vw;
  left: 4vw;
  content: "";
  width: 2vw;
  height: 2vw;
  border-radius: 50%;
  background-color: #67382a34;
}
.intro:nth-child(2)::after {
  position: absolute;
  top: 1.8vw;
  left: 1.5vw;
  content: "";
  width: 2vw;
  height: 2vw;
  border-radius: 50%;
  background-color: #67382a34;
}
.intro:nth-child(3)::after {
  position: absolute;
  top: 4vw;
  left: 1.5vw;
  content: "";
  width: 2vw;
  height: 2vw;
  border-radius: 50%;
  background-color: #67382a34;
}

/* mid */

.companyMidBg {
  background-color: #f6f6f6;
}
.companyMid {
  margin: 0 15%;
  padding: 5vw 0;
}

.flex2 {
  display: flex;
  align-items: center;
  justify-content: center;
}

.imgBox {
  z-index: 1;
  width: 100%;
  position: relative;
}
.imgBox h2 {
  margin-top: 3vw;
  margin-left: 7vw;
  font-weight: bold;
  font-size: 1.6vw;
}

.imgBox img {
  width: 16vw;
  height: 16vw;
  border-radius: 50%;
}
.imgBox::after {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 5vw;
  content: "";
  width: 16vw;
  height: 16vw;
  background-color: #eeeceb;
  border-radius: 50%;
}
/* Bottom */
.companyBottom {
  margin: 0 15%;
  padding: 5vw 0;
}
.numBox {
  text-align: center;
  margin: 2vw;
}
.numBox:nth-child(2) {
  border-left: 1px solid rgba(0, 0, 0, 0.2);
  padding: 0 5vw;
}
.numBox h2 {
  font-weight: bold;
}
.numBox h3 {
  font-size: 3.1vw;
}

/* 애니메이션 */
/* 애니메이션 정의 */
@keyframes slide-up {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* 텍스트 애니메이션 클래스 */
@keyframes slideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.active .companyTop h1 {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
}

.active .companyTop p {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
  animation-delay: 0.2s;
}

.active .companyTop .intro:nth-of-type(1) {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
  animation-delay: 0.4s;
}

.active .companyTop .intro:nth-of-type(2) {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
  animation-delay: 0.6s;
}

.active .companyTop .intro:nth-of-type(3) {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
  animation-delay: 0.8s;
}

