/* main6.module.css */

.main6 {
  width: 100%;
  height: 100vh;
  background-color: #f5f5f5;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main6Box {
  display: flex;
  width: 100%;
}

.btn {
  display: flex;
  justify-content: left;
}
.btn img {
  object-fit: contain;
  width: 2vw;
  height: 2vw;
  margin-right: 1vw;
  margin-top: 1vw;
  cursor: pointer;
}
.txtBox {
  text-align: left;
  width: 22%;
  position: relative;
  left: 10%;
  line-height: 1.8;
}

.txtBox h1 {
  font-weight: bold;
  margin-top: 2vw;
}
.partner {
  height: 360px;
  background-color: rgb(255, 255, 255);
  object-fit: cover;
  border-radius: 20px;
}
