/* main4.module.css */
.main4 {
  background-color: #f5f5f5;
  width: 100%;
  height: 100%;
  display: grid;
  text-align: left;
}

.main4 h1 {
  width: 31.3vw;
  font-weight: bold;
  font-size: 2.3vw;
}

.main4 h2 {
  width: 20.8vw;
  font-size: 1vw;
  color: #666666;
}

.main4Flex {
  width: 75%;
  margin: 0 10%;
  margin-top: 3vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.btn {
  position: relative;
  top: 1vw;
  width: 2.4vw;
  height: 2.4vw;
}

svg {
  fill: rgb(39, 24, 5) !important;
}

.productBox {
  position: relative;
  border-radius: 10px;
  text-align: left;
}
.products {
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
  width: 15vw;
}
.productBox img {
  position: relative;
  width: 15vw;
  height: 15vw;
  object-fit: contain;
}
.txtBox {
  line-height: 2;
}
.txtBox h3 {
  font-size: 1.2vw;
  font-weight: bold;
}

.txtBox a {
  font-size: 1vw;
  transition: 0.2s;
}
.txtBox a:hover {
  font-weight: bold;
}

/* 애니메이션 */
@keyframes slideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.active h1 {
  opacity: 0;
  animation: slideUp 1s forwards;
}
.active h1 {
  animation-delay: 0.5s;
}

.active h2 {
  opacity: 0;
  animation: slideUp 1s forwards;
  animation-delay: 1s;
}
.active .btn {
  opacity: 0;
  animation: slideUp 1s forwards;
  animation-delay: 1.3s;
}
.active .productBox:nth-child(1) {
  opacity: 0;
  animation: slideUp 1s forwards;
  animation-delay: 1.6s;
}
.active .productBox:nth-child(2) {
  opacity: 0;
  animation: slideUp 1s forwards;
  animation-delay: 1.9s;
}
.active .productBox:nth-child(3) {
  opacity: 0;
  animation: slideUp 1s forwards;
  animation-delay: 2.1s;
}
.active .productBox:nth-child(4) {
  opacity: 0;
  animation: slideUp 1s forwards;
  animation-delay: 2.3s;
}
.active .productBox:nth-child(5) {
  opacity: 0;
  animation: slideUp 1s forwards;
  animation-delay: 2.6s;
}
