/* main8.module.css */

.main8 {
  position: relative;
  width: 100%;
  height: 100vh;
}

.main8 h1,
.main8 h3 {
  font-weight: bold;
}
.main8Box {
  position: relative;
  left: 10%;
  top: 30%;
}
.main8txtBox {
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: left;
  margin-bottom: 3vw;
}
.main8txtBox h1 {
  font-size: 2.3vw;
}
.main8txtBox h2 {
  font-size: 1.3vw;
}
.main8txtBox a {
  text-align: center;
  line-height: 2.3;
  background-color: brown;
  color: white;
  font-weight: 600;
  font-size: 1vw;
  width: 8.4vw;
  height: 2.4vw;
  border-radius: 25px;
}
.newsBox {
  width: 100%;
}
.newsItem {
  width: 40%;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  border-bottom: 1px solid;
  line-height: 4;
  text-transform: uppercase;
}
.newsItem h3 {
  font-size: 1.1vw;
}
.newsItem a {
  font-size: 0.8vw;
}
.newsItem:last-child {
  border: none;
}
.linkBox img {
  width: 2vw;
  height: 2vw;
}
.main8 em {
  margin-left: 0.5vw;
}
.recruit {
  width: 28.4vw;
  height: 12vw;
  background: url(../../images//recruit.png) no-repeat center/cover;
  border-radius: 25px;
  margin-bottom: 1.5vw;
}
.support {
  width: 28.4vw;
  height: 12vw;
  background: url(../../images//support.png) no-repeat center/cover;
  border-radius: 25px;
}
.linkBox {
  position: absolute;
  top: 30%;
  right: 15%;
  line-height: 2;
}
.linkBox h3 {
  text-align: left;
  color: white;
  font-size: 1.6vw;
}
.recruit img {
  width: 46px;
  height: 58px;
  position: relative;
  right: 2vw;
}
.support img {
  width: 51px;
  height: 58px;
  position: relative;
  right: 2vw;
}
.support,
.recruit {
  display: flex;
  justify-content: center;
  align-items: center;
}

.link a {
  display: block;
  background-color: #fff;
  width: 9.4vw;
  height: 2.7vw;
  line-height: 2.5;
  border-radius: 25px;
  font-size: 0.95vw;
}
.link button {
  display: block;
  background-color: #fff;
  width: 9.8vw;
  height: 2.7vw;
  line-height: 2.5;
  border-radius: 25px;
  font-size: 1vw;
  cursor: pointer;
  font-weight: bold;
}

.main8Box,
.linkBox {
  transition: 0.5s;
}

.toggle .main8Box,
.toggle .linkBox {
  margin-top: -20vh;
}
.toggleFooter {
  margin-bottom: 28vw;
}
.btn {
  width: 3vw;
  height: 3vw;
  position: relative;
  top: 30%;
  left: 3%;
  animation: down 1.5s infinite;
  -webkit-animation: down 1.5s infinite;
  cursor: pointer;
  background: url(../../images/downArrow.svg) center/cover no-repeat;
  transition: 0.3s;
}

@keyframes down {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translateY(15px);
  }
  40% {
    transform: translate(0);
  }
}
@-webkit-keyframes down {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translateY(15px);
  }
  40% {
    transform: translate(0);
  }
}
.btn.toggle {
  top: 28%;
  background: url(../../images/downArrow.svg) center/cover no-repeat;
  rotate: 180deg;
}
@keyframes slideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.active h1 {
  opacity: 0;
  animation: slideUp 1s forwards;
}

.active h2 {
  opacity: 0;
  animation: slideUp 1s forwards;
}
.active h2 {
  animation-delay: 0.5s;
}
.active .main8txtBox a {
  opacity: 0;
  animation: slideUp 1s forwards;
}
.active .main8txtBox a {
  animation-delay: 0.8s;
}

.active .newsItem:nth-child(1),
.active .newsItem:nth-child(2),
.active .newsItem:nth-child(3),
.active .newsItem:nth-child(4),
.active .newsItem:nth-child(5) {
  opacity: 0;
  animation: slideUp 1s forwards;
}
.active .newsItem:nth-child(1) {
  animation-delay: 1s;
}
.active .newsItem:nth-child(2) {
  animation-delay: 1.2s;
}
.active .newsItem:nth-child(3) {
  animation-delay: 1.4s;
}
.active .newsItem:nth-child(4) {
  animation-delay: 1.6s;
}
.active .newsItem:nth-child(5) {
  animation-delay: 1.8s;
}

.active .linkBox {
  opacity: 0;
  animation: slideUp 1s forwards;
  transition: 0s;
}
.active .linkBox {
  animation-delay: 1.5s;
}
