.ask {
  width: 100%;
  text-align: left;
}

.askBox {
  margin: 0 15%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.ask img {
  width: 2vw;
  height: 2vw;
}
.svgBox {
  display: flex;
}

.svgBox img {
  margin-right: 1vw;
}

.txtBox {
  height: 100vh;
}
.txtBox p {
  font-size: 1.3vw;
}

.txt {
  line-height: 2;
  margin-bottom: 5vw;
}
.txt h1 {
  font-weight: bold;
  font-size: 2.3vw;
}
.svgBox {
  margin: 2vw 0;
}

.form {
  width: 33vw;
}

.formBox {
  margin: 5vw 0;
  margin-top: 10vw;
}

.ask input[type="text"] {
  width: 33.1vw;
  height: 3.4vw;
  border: 1px solid #c8c8c8;
  font-size: 1vw;
  padding-left: 1vw;
  border-radius: 10px;
  margin-bottom: 1.5vw;
}
.ask textarea {
  resize: none;
  width: 33.1vw;
  height: 22.4vw;
  border: 1px solid #c8c8c8;
  font-size: 1vw;
  padding-left: 1vw;
  padding-top: 1vw;
  border-radius: 20px;
  margin-bottom: 2vw;
}

.formSubmit button {
  padding: 1vw 14.2vw;
  border-radius: 15px;
  font-size: 1.2vw;
  cursor: pointer;
  background-color: #67382a;
  color: white;
}
.ask input::placeholder,
.ask textarea::placeholder {
  color: #c8c8c8;
}
/* 애니메이션 */

/* ask.module.css */

@keyframes slideInFromLeft {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInFromRight {
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.txtBox {
  animation: slideInFromLeft 1s ease-out forwards;
}

.formBox {
  animation: slideInFromRight 1s ease-out forwards;
}
