/* footer.module.css */

.footerMain {
  width: 100%;
  height: 30vh;
  background-color: #2c2c2c;
  color: white;
  position: absolute;
  margin-top: 130vh;
  transition: 0.5s;
}

.footerMain h1 {
  font-size: 16px;
  text-transform: uppercase;
  font-weight: bold;
}
.footerMain p {
  font-size: 14px;
}
.footerMain h3 {
  font-weight: lighter;
}

.footerMainBox {
  position: relative;
  top: 2vw;
  width: 80%;
  display: flex;
  justify-content: space-between;
  text-align: left;
  margin-left: 10%;
  line-height: 2;
}

.logos img {
  width: 6vw;
  height: 4.6vw;
}
.sns img {
  width: 1.5vw;
  height: 1.5vw;
  margin-right: 0.5vw;
  cursor: pointer;
}
.sns {
  display: flex;
  margin-top: 2vw;
}
.footerMain h1 {
  margin-bottom: 1vw;
}
.copyright {
  line-height: 2.5;
  position: relative;
  top: 3vw;
  border-top: 1px solid #666666;
  font-size: 12px;
}

