.faq {
  width: 100%;
  height: 100%;
  text-align: left;
  margin-bottom: 2vw;
}
.faqBox {
  margin: 0 15%;
  margin-bottom: 10vw;
}

.faqBox h1,
.faqBox h2 {
  margin-top: 2vw;
  font-weight: bold;
}

.faqBox h1 {
  font-size: 2.3vw;
}
.faqBox h2 {
  font-size: 1.3vw;
}

.faqBox p {
  font-size: 1.3vw;
  color: #444444;
  font-weight: light;
  margin-top: 1vw;
}
.toggleBox p {
  font-size: 1.1vw;
}

.btn {
  background: url(../../images/plus.svg) center/cover no-repeat;
  width: 1.2vw;
  height: 1.2vw;
  cursor: pointer;
}
.toggles {
  margin-top: 3vw;
}

.toggleBox {
  display: flex;
  align-items: center;
  border-top: 1px solid #00000030;
}
.toggleBox .txtBox {
  margin-right: 30vw;
}
.toggleBox:first-child {
  border: none;
}

.txt {
  width: 110%;
  height: 0;
  opacity: 0;
  transition: 0.2s;
}

.toggleBox:last-child {
  border-bottom: 1px solid #00000030;
}

.txtBox h2 {
  margin: 1vw 0;
}

.txt.active {
  height: 8vh;
  opacity: 1;
}

.btn.active {
  background: url(../../images/minus.svg) center/cover no-repeat;
  width: 1.2vw;
  height: 1.4vw;
}
/* 애니메이션 */

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.faqBoxTxt h1,
.faqBoxTxt p,
.toggleBox {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
}
.faqBoxTxt h1 {
  animation-delay: 0.2s;
}

.faqBoxTxt p {
  animation-delay: 0.4s;
}
.toggleBox:nth-child(1) {
  animation-delay: 0.6s;
}
.toggleBox:nth-child(2) {
  animation-delay: 0.8s;
}
.toggleBox:nth-child(3) {
  animation-delay: 1s;
}
