.business {
  width: 100%;
}

.businessBox {
  margin: 0 15%;
}
.businessBox p {
  font-size: 1.3vw;
}
.txtBox {
  text-align: center;
  margin-bottom: 20px;
  text-align: left;
}

.txtBox h1 {
  font-weight: bold;
  font-size: 1.6vw;
  margin: 2vw 0;
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, auto);
  line-height: 2;
  margin-bottom: 4vw;
}

.gridBox {
  text-align: center;
  text-align: left;
  margin: 1vw 0;
}
.gridBox h2 {
  font-weight: bold;
  margin-top: 1vw;
  font-size: 1.2vw;
}
.gridBox p {
  font-size: 1vw;
}
.imgBox {
  background-color: #efefef;
  border-radius: 15px;
  width: 20.8vw;
  height: 20.8vw;
}
.imgBox img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  object-fit: contain;
  scale: 0.8;
}
/* 애니메이션 */

@keyframes slideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.gridBox .imgBox {
  opacity: 0; /* Initially hidden */
  transform: translateY(100px); /* Start position */
}

.gridBox.active .imgBox {
  opacity: 1; /* Visible when active */
  animation: slideUp 0.5s ease-out forwards;
}
.gridBox h2 {
  opacity: 0;
  transform: translateY(100px); /* Start position */
}

.gridBox.active h2 {
  transform: translateY(0);
  animation: slideUp 0.5s ease-out forwards;
  animation-delay: 0.3s;
}
.gridBox p {
  opacity: 0;
  transform: translateY(100px); /* Start position */
}

.gridBox.active p {
  transform: translateY(0);
  animation: slideUp 0.5s ease-out forwards;
  animation-delay: 0.6s;
}
