.video {
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 100%;
  /* 화면채우는요소 */
  object-fit: cover;
}
.main5 {
  width: 100%;
  color: white;
}
.main5 h1 {
  font-size: 3vw;
  font-weight: bold;
}
@keyframes slideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.active h1 {
  opacity: 0;
  animation: slideUp 1s forwards;
}
.active h1 {
  animation-delay: 0.5s;
}
