.news {
  width: 100%;
  text-align: left;
}
.newsBox {
  margin: 0 15%;
}

.txtBox {
  margin: 3vw 0;
}

.txtBox h1 {
  font-weight: bold;
  font-size: 2.3vw;
}
.txtBox p {
  font-size: 1.3vw;
}

.newsGridContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.newsGrid img {
  width: 32vw;
  height: 20.8vw;
  border-radius: 15px;
  margin: 1vw 0;
}

.newsGrid h2 {
  font-weight: bold;
  font-size: 1.4vw;
}
.pagination {
  display: flex;
  justify-content: center;
  margin: 4vw;
}

.pagination button {
  margin: 0 10px;
  padding: 0.6vw 0.8vw;
  border-radius: 10px;
  border: none;
  border: 1px solid #afafaf;
  color: #afafaf;
  background-color: transparent;
  cursor: pointer;
}

.pagination button:hover {
  background-color: #ddd;
}

.pagination .active {
  background-color: #afafaf;
  color: white;
}
/* 애니메이션 */

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.txtBox h1,
.txtBox p,
.newsGrid img,
.newsGrid h2 {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
}

.txtBox h1 {
  animation-delay: 0.2s;
}

.txtBox p {
  animation-delay: 0.4s;
}

.newsGrid:nth-child(1) img,
.newsGrid:nth-child(1) h2 {
  animation-delay: 0.6s;
}
.newsGrid:nth-child(2) img,
.newsGrid:nth-child(2) h2 {
  animation-delay: 0.6s;
}
.newsGrid:nth-child(3) img,
.newsGrid:nth-child(3) h2 {
  animation-delay: 0.8s;
}
.newsGrid:nth-child(4) img,
.newsGrid:nth-child(4) h2 {
  animation-delay: 0.8s;
}
