/* nav.module.css */

.navBar {
  z-index: 9999;
  width: 80%;
  position: absolute;
  top: 28px;
  display: flex;
  align-items: last baseline;
}

.navBar h1 {
  /* 160.8px */
  width: 160.8px;
  background: url(../../images/logo.png) center/contain no-repeat; /* 로고 이미지 배경으로 설정 */
  color: transparent;
  margin-right: 10.1vw;
}

.navBar .link {
  margin: 0 2.4vw;
  font-weight: bold;
  font-size: 1vw;
}
.activeNav h1 {
  margin-right: 5vw;
}
.activeNav {
  color: white;
  left: 15%;
}

.activeNav h1 {
  background: url(../../images/logo2.png) center/contain no-repeat; /* 로고 이미지 배경으로 설정 */
}
