/* footer.module.css */

.footer {
  width: 100%;
  height: 30vh;
  background-color: #2c2c2c;
  color: white;
  transition: 0.5s;
}

.footer h1 {
  font-size: 16px;
  text-transform: uppercase;
  font-weight: bold;
}
.footer p {
  font-size: 14px;
}
.footer h3 {
  font-weight: lighter;
}

.footerBox {
  position: relative;
  top: 3vw;
  display: flex;
  justify-content: space-between;
  text-align: left;
  margin: 0 15%;
}

.logos img {
  width: 6vw;
  height: 4.6vw;
}

.txtBox {
  font-size: 0.9vw;
}
.txtBox p:nth-child(2) {
  margin: 1vw 0;
}
.sns img {
  width: 1.5vw;
  height: 1.5vw;
  margin-right: 0.5vw;
  cursor: pointer;
}
.sns {
  display: flex;
  margin-top: 2vw;
}
.footer h1 {
  margin-bottom: 1vw;
}
.copyright {
  background-color: #2c2c2c;
  color: white;
  line-height: 2.5;
  border-top: 1px solid #666666;
  font-size: 12px;
}

