.management {
  width: 100%;
  text-align: left;
}

.managementTop img {
  border-radius: 25px;
}

.managementMid {
  margin-bottom: 10vw;
}

.managementBottom {
  position: relative;
  z-index: 1;
  color: white;
}

.video {
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 101%;
  /* 화면채우는요소 */
  object-fit: cover;
}
.managementMid img {
  border-radius: 25px;
}

.managementBox {
  margin: 0 15%;
}

.txtBox {
  margin: 3vw 0;
}

.txtBox h2 {
  font-size: 1vw;
  font-weight: bold;
}
.txtBox h1 {
  font-size: 2.3vw;
  font-weight: bold;
}

.txtBox2 p {
  font-size: 1.3vw;
  font-weight: light;
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flex2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 15%;
  height: 60vh;
}

.line {
  width: 100%;
  height: 1px;
  background: #00000040;
  margin: 5vw 0;
}
.managementBottom a {
  position: relative;
  top: 2vw;
  padding: 0.5vw 2vw;
  border-radius: 25px;
  border: 1px solid white;
  font-weight: bold;
}
.managementBottom .txtBox {
  position: relative;
}
.managementBottom .txtBox::after {
  content: "";
  position: absolute;
  top: -5vw;
  left: 250%;
  width: 1px;
  height: 15vw;
  background-color: rgba(255, 255, 255, 0.5);
}
/* 애니메이션 */

.management {
  position: relative;
  overflow: hidden;
}

.managementTop .txtBox {
  animation: slideUp 0.5s ease-out forwards;
}
.managementTop .txtBox2 {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
  animation-delay: 0.3s;
}
.managementTop img {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
  animation-delay: 0.6s;
}
.managementMid .txtBox {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
  animation-delay: 1s;
}
.managementMid .txtBox2 {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
  animation-delay: 1.3s;
}
.managementMid img {
  opacity: 0;
  animation: slideUp 0.5s ease-out forwards;
  animation-delay: 1.6s;
}

/* Keyframes for the animations */
@keyframes slideUp {
  from {
    transform: translateY(100px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
