.main1 {
  width: 100%;
  height: 100vh;
  background: url(../../images/main.png) center/cover no-repeat;
  display: flex;
  align-items: center;
}
.btn {
  width: 3vw;
  height: 3vw;
  position: relative;
  top: 30%;
  animation: down 1.5s infinite;
  -webkit-animation: down 1.5s infinite;
  cursor: pointer;
  background: url(../../images/downArrow2.svg) center/cover no-repeat;
  transition: 0.3s;
}

.txtBox {
  color: white;
  text-align: left;
  /* 10.9 vw = 209px */
  margin-left: 10.9vw;
}
.txtBox h1 {
  /* 3.6 vw = 70px */
  font-size: 3.6vw;
  font-weight: bold;
  /* 39.1vw = 750px */
  width: 39.1vw;
}
.txtBox p {
  /* 25px = 1.3vw */
  font-size: 1.3vw;
}

/* 애니메이션 효과 */
@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.txtBox p:nth-child(1) {
  opacity: 0;
  animation: slideUp 1s ease forwards;
  animation-delay: 0.2s; /* 첫 번째 요소 애니메이션 딜레이 */
  font-weight: 600;
}

.txtBox h1 {
  opacity: 0;
  animation: slideUp 1s ease forwards;
  animation-delay: 0.4s; /* 두 번째 요소 애니메이션 딜레이 */
}

.txtBox p:nth-child(3) {
  opacity: 0;
  font-size: 1.1vw;
  animation: slideUp 1s ease forwards;
  animation-delay: 0.6s; /* 세 번째 요소 애니메이션 딜레이 */
}
@keyframes down {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translateY(15px);
  }
  40% {
    transform: translate(0);
  }
}
@-webkit-keyframes down {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translateY(15px);
  }
  40% {
    transform: translate(0);
  }
}
