.main3 {
  text-align: left;
  width: 80%;
}
.main3 h2,
.main3 h1 {
  font-weight: bold;
}
.main3 h1 {
  margin-bottom: 4.5vw;
}
.main3Flex {
  display: flex;
  justify-content: left;
}
.main3List1,
.main3List2,
.main3List3 {
  margin-right: 3vw;
  line-height: 3vw;
  transition: 0.3s;
}
.main3List1 img,
.main3List2 img,
.main3List3 img {
  border-radius: 1vw;
  width: 21.5vw;
  height: 15.2vw;
  margin-bottom: 1vw;
  transition: 0.3s;
}
/* 이미지 호버효과 */
.main3List1:hover,
.main3List2:hover,
.main3List3:hover {
  scale: 1.2;
}

/* 스와이퍼 애니메이션 - active */

@keyframes slideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.active h1 {
  opacity: 0;
  animation: slideUp 1s forwards;
}

.active h1 {
  animation-delay: 0.5s;
}

.active .main3List1 {
  opacity: 0;
  animation: slideUp 1s forwards;
  animation-delay: 1s;
}
.active .main3List2 {
  opacity: 0;
  animation: slideUp 1s forwards;
  animation-delay: 1.3s;
}
.active .main3List3 {
  opacity: 0;
  animation: slideUp 1s forwards;
  animation-delay: 1.6s;
}
.active h2,
.active p {
  opacity: 0;
  animation: slideUp 1s forwards;
}
.active h2 {
  animation-delay: 1.7s;
}

.active p {
  animation-delay: 2s;
}
