.history {
  width: 100%;
  text-align: left;
}
.history h1 {
  font-weight: bold;
  font-size: 2.3vw;
  margin-bottom: 1vw;
}
.history p {
  font-size: 1.3vw;
}
.flex h1 {
  font-size: 1.8vw;
}
.historyTop {
  margin: 0 15%;
  margin-top: 2vw;
}
.flex {
  display: flex;
  text-align: left;
  justify-content: left;
  align-items: center;
}
.history img {
  width: 33.2vw;
  height: 26.6vw;
  border-radius: 15px;
  margin-right: 8vw;
}
.line {
  position: relative;
  right: 2.8vw;
  margin: 3vw;
  width: 40vw;
  height: 1px;
  background-color: #00000030;
}

.history ul {
  padding: 0;
  margin: 0;
  line-height: 2;
}

.history li {
  padding: 0;
  margin: 1.5vw;
  list-style: disc;
  font-size: 1.3vw;
  color: #444444;
}
.history li::marker {
  color: #878787;
}

/* history.module.css */

/* General Styles */
.history {
  position: relative;
  overflow: hidden; /* Ensure that animations are contained */
}

/* History Top Section Animation */
.historyTop h1,
.historyTop p {
  opacity: 0;
  transform: translateY(20px);
  animation: slideUp 0.5s ease-out forwards;
}

.historyTop h1 {
  animation-delay: 0.5s;
}

.historyTop p {
  animation-delay: 1s;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
