.customer {
  width: 100%;
  height: 100%;
  text-align: center;
}
.customerTop {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 30vh;
  background: url(../../images/customerBg.png) no-repeat center/cover;
}
.customerTop h1,
.customerTop h2 {
  z-index: 1;
  font-weight: bold;
}
.customerTop h2 {
  font-weight: 400;
  font-size: 0.9vw;
}
.customerTop {
  color: white;
}

.customerTop::after {
  position: absolute;
  width: 100%;
  height: 100%;
  content: "";
  background-color: rgba(0, 0, 0, 0.5);
}
.customerFlex {
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid #c8c8c8;
  height: 5vh;
}
.customerFlex a {
  transition: 0.3s;
  width: 23.2%;
  height: 110%;
  color: #878787;
  line-height: 2.5;
  border-bottom: 2px solid #c8c8c8;
  font-size: 1vw;
  font-weight: 500;
}
.customerFlex a:hover {
  border-bottom: 2px solid black;
  color: black;
}
.customerFlex a.active {
  border-bottom: 2px solid black;
  color: black;
}
