.promote {
  width: 100%;
  height: 100%;
  text-align: center;
}
.promoteTop {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 30vh;
  background: url(../../images/promoteBg.png) no-repeat center/cover;
}
.promoteTop h1,
.promoteTop h2 {
  z-index: 1;
  font-weight: bold;
}
.promoteTop h2 {
  font-weight: 400;
  font-size: 0.9vw;
}
.promoteTop {
  color: white;
}

.promoteTop::after {
  position: absolute;
  width: 100%;
  height: 100%;
  content: "";
  background-color: rgba(0, 0, 0, 0.5);
}
.promoteFlex {
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid #c8c8c8;
  height: 5vh;
}
.promoteFlex a {
  transition: 0.3s;
  width: 18%;
  height: 110%;
  color: #878787;
  line-height: 2.5;
  border-bottom: 2px solid #c8c8c8;
  font-size: 1vw;
  font-weight: 500;
  margin: 0 10vw;
}
.promoteFlex a:hover {
  border-bottom: 2px solid black;
  color: black;
}
.promoteFlex a.active {
  border-bottom: 2px solid black;
  color: black;
}
