/* style.css */
#app {
  height: 100%;
}
html,
body {
  position: relative;
  height: 100%;
}

body {
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.mainSwiper {
  width: 100%;
  height: 100vh;
}
/* historySwiper */
.historySwiper .swiper-slide {
  margin: 0 15%;
  display: flex;
  justify-content: left;
  align-items: center;
}

.historySwiper .swiper-slide img {
  position: relative;
  opacity: 0;
  animation: slideRight 0.5s ease-out forwards;
  animation-delay: 1.5s;
}

.historySwiper .swiper-slide h1 {
  opacity: 0;
  transform: translateX(50px);
  animation: slideLeft 0.5s ease-out forwards;
  animation-delay: 1.5s;
}
.historySwiper .swiper-slide ul {
  opacity: 0;
  transform: translateX(50px);
  animation: slideLeft 0.5s ease-out forwards;
  animation-delay: 1.7s;
}

@keyframes slideLeft {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes slideRight {
  from {
    opacity: 0;
    left: -100px;
  }
  to {
    opacity: 1;
    left: 0px;
  }
}

/* historySwiper */

.main4Swiper {
  position: relative;
  width: 75%;
  margin-right: 15%;
}

.main4Swiper .swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 390px;
}
.historySwiper {
  width: 100%;
  height: 80vh;
}

.main6Swiper {
  position: relative;
  left: 13vw;
}

.swiper-pagination-bullet {
  background-color: black;
  width: 6px;
  height: 6px;
  opacity: 0.6;
}
.swiper-pagination-bullet-active {
  scale: 2;
  background-color: transparent;
  border: 1px solid;
}
.main4Swiper .swiper-scrollbar-horizontal {
  left: 0 !important;
  bottom: 6vw !important;
  width: 100% !important;
  background-color: #cccccc !important;
  height: 2px !important;
}
